/* eslint-disable object-shorthand */
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { EventsService } from 'src/app/services/events.service';
import { LocalService } from 'src/app/services/local.service';
import { RestService } from 'src/app/services/rest.service';
import { UtilityService } from 'src/app/services/utility.service';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';

@Component({
  selector: 'app-cleanerhome',
  templateUrl: './cleanerhome.page.html',
  styleUrls: ['./cleanerhome.page.scss'],
})
export class CleanerhomePage implements OnInit {
  loggedCleaner: any;
  segment: any = 'upcoming';

  bookings: any = [];
  currency: any;

  slideOptions = {
    initialSlide: 0,
    autoplay: true,
    loop: true,
    speed: 2000,
    effect: 'fade',
  };

  showBanner = false;
  banners = [];
  constructor(
    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private iab: InAppBrowser
  ) {
    this.loggedCleaner = this.localService.getCleaner();
    this.currency = this.localService.getSettings().currency_symbol;

    if (
      this.localService.getSettings().hasOwnProperty('show_banner_cleaners') &&
      this.localService.getSettings().show_banner_cleaners === '1'
    ) {
      this.restService.getData('cleaner/banners').subscribe(
        (data) => {
          this.banners = data;
          if (data.length > 0) {
            this.showBanner = true;
          }
        },
        (err) => {}
      );
    }
  }

  ngOnInit() {}

  ionViewWillEnter() {
    this.getData();
  }
  bookingDetails(id, type) {
    if (type === 'booking') {
      // type is booking
      this.restService.getData('jobdetails/' + id).subscribe(
        (data) => {
          this.router.navigate(['/bookingdetails'], {
            queryParams: {
              id: data.id,
              type: type,
              data: JSON.stringify(data),
            },
          });
        },
        (err) => {}
      );
    } else {
      // type is subscription
      this.restService.getData('jobhistory/' + id).subscribe(
        (data) => {
          this.router.navigate(['/bookingdetails'], {
            queryParams: {
              id: data.id,
              type: type,
              data: JSON.stringify(data),
            },
          });
        },
        (err) => {}
      );
    }
  }
  openNotifications() {
    this.router.navigate(['/cleanernotifications']);
  }

  getData() {
    this.loggedCleaner = this.localService.getCleaner();
    this.utilityService.loadingOpen('Loading...');
    this.restService
      .getData('cleaner/' + this.loggedCleaner.id + '/bookings')
      .subscribe(
        (data) => {
          this.bookings = data;
          this.utilityService.loadingClose();
        },
        (err) => {
          // handle error
          this.utilityService.loadingClose();
        }
      );
  }

  doRefresh(event) {
    this.restService
      .getData('cleaner/' + this.loggedCleaner.id + '/bookings')
      .subscribe(
        (data) => {
          this.bookings = data;
          event.target.complete();
        },
        (err) => {
          // handle error
          event.target.complete();
        }
      );
  }

  openLink(link) {
    if (link && link !== '' && link !== '#') {
      this.iab.create(link, '_blank', {
        location: 'no',
        hardwareback: 'no',
        hidenavigationbuttons: 'yes',
        hideurlbar: 'yes',
        fullscreen: 'yes',
        zoom: 'no',
        clearcache: 'yes',
        toolbar: 'yes',
      });
    }
  }
}
